export default "<template>\n  <Sandbox\n    id=\"example\"\n    title=\"Example\"\n  >\n    <Preview>\n      <VInput\n        v-model=\"text\"\n        :bordered=\"bordered\"\n        :round=\"round\"\n        :rounded=\"rounded\"\n        :rounded-lg=\"roundedLg\"\n        :dark=\"dark\"\n        :light=\"light\"\n        :disabled=\"disabled\"\n        :left-icon=\"leftIcon !== 'none' ? leftIcon : null\"\n        :right-icon=\"rightIcon !== 'none' ? rightIcon : null\"\n        :error=\"error\"\n        :type=\"type\"\n      />\n    </Preview>\n    <template #controls>\n      <Knobs>\n        <KnobListItem\n          v-model=\"type\"\n          :items=\"types\"\n          label=\"Type\"\n        />\n        <KnobBoolean\n          v-model=\"dark\"\n          label=\"Dark\"\n        />\n        <KnobBoolean\n          v-model=\"light\"\n          label=\"Light\"\n        />\n        <KnobBoolean\n          v-model=\"bordered\"\n          label=\"Bordered\"\n        />\n        <KnobBoolean\n          v-model=\"rounded\"\n          label=\"Rounded\"\n        />\n        <KnobBoolean\n          v-model=\"roundedLg\"\n          label=\"Rounded large\"\n        />\n        <KnobBoolean\n          v-model=\"round\"\n          label=\"Round\"\n        />\n        <KnobBoolean\n          v-model=\"disabled\"\n          label=\"Disabled\"\n        />\n        <KnobListItem\n          v-model=\"leftIcon\"\n          :items=\"icons\"\n          label=\"Left icon\"\n        />\n        <KnobListItem\n          v-model=\"rightIcon\"\n          :items=\"icons\"\n          label=\"Right icon\"\n        />\n        <KnobString\n          v-model=\"error\"\n          label=\"Error\"\n        />\n      </Knobs>\n    </template>\n  </Sandbox>\n</template>\n\n<script>\nimport {\n  Preview,\n  Sandbox,\n  Knobs,\n  KnobListItem,\n  KnobBoolean,\n  KnobString\n} from '@/docs/components';\nimport {\n  VInput\n} from '@/components';\nimport { icons } from '@/docs/helpers/story-params';\n\nexport default {\n  name: 'InputExample',\n  components: {\n    Preview,\n    Sandbox,\n    Knobs,\n    KnobListItem,\n    KnobBoolean,\n    KnobString,\n    VInput\n  },\n  data () {\n    return {\n      icons,\n      types: [\n        'button',\n        'checkbox',\n        'color',\n        'date',\n        'datetime-local',\n        'email',\n        'file',\n        'hidden',\n        'image',\n        'month',\n        'number',\n        'password',\n        'radio',\n        'range',\n        'reset',\n        'search',\n        'submit',\n        'tel',\n        'text',\n        'time',\n        'url',\n        'week'\n      ],\n      text: 'some text...',\n      bordered: false,\n      round: false,\n      rounded: true,\n      roundedLg: false,\n      roundedSmall: false,\n      dark: false,\n      light: true,\n      disabled: false,\n      leftIcon: 'none',\n      rightIcon: 'none',\n      error: '',\n      hint: '',\n      type: 'text'\n    };\n  }\n};\n</script>\n";